<template>
  <div class="covid-sim">
    <h1>COVID SIM</h1>
    <!--
    <dat-gui
      v-if="false"
      closeText="Close controls"
      openText="Open controls"
      closePosition="bottom">
      <dat-number v-model="h_good" show-slider :min="0" :max="500" :step="1" label="h_good"/>
      <dat-number v-model="r_good" show-slider :min="0" :max="1" :step="0.001" label="r_good"/>
      <dat-boolean v-model="status" label="status"/>
    </dat-gui>
    -->
    <div class="controls">
      <label for="slider1">
        <vue-slider v-model="params.infection_radius" :min="0" :max="50" :interval="1" name="slider1" :tooltip="'always'" />
        <span class="label">Infection Radius</span>
      </label>
      <label for="slider2">
        <vue-slider v-model="params.healing_time" :min="0" :max="200" :interval="10" name="slider2" :tooltip="'always'" />
        <span class="label">Healing Time</span>
      </label>
      <label for="checkbox1">
        <input type="checkbox" v-model="params.enable_debug" name="checkbox1" />
        <span class="label">Enable Debug</span>
      </label>
      <pre style="float:right; margin-right: 1rem">time = {{time}}</pre>
      <div class="buttons">
        <button @click="run()" v-if="!status">Continue</button>
        <button @click="stop()" v-if="status">Pause</button>
        <button @click="step(); draw()" :disabled="status">Step</button>
        <button @click="reset()">Reset</button>
      </div>
    </div>
    <div id="canvas-container"></div>
    <div id="canvas-container-graph"></div>
    <!-- status: {{status}} -->
    <div class="flex">
      <span class="total"># Total: {{numTotal}}</span>
      <span class="healed"># Healed: {{numHealed}}</span>
      <span class="infected"># Infected: {{numInfected}}</span>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import Two from 'two.js'
import * as math from 'mathjs'
import VueSlider from 'vue-slider-component'
import config from './simulator/config'
import Agent from './simulator/agent'
import Environment from './simulator/environment'

// import DatGui from '@cyrilf/vue-dat-gui'
// Vue.use(DatGui)

export default {
  components: {
    VueSlider,
  },
  data() {
    return {
      time: 0,
      params: {
        infection_radius: config.INFECTION_RADIUS,
        healing_time: config.HEALING_TIME,
        enable_debug: true,
      },
      numTotal: 0,
      numInfected: 0,
      numHealed: 0,
      status: true,
    }
  },
  mounted() {
    this.two = new Two({
      width: 800,
      height: 480,
      type: Two.Types.canvas
    }).appendTo(document.getElementById('canvas-container'));

    this.progressCanvas = new Two({
      width: 800,
      height: 80,
      type: Two.Types.canvas
    }).appendTo(document.getElementById('canvas-container-graph'));

    // this.two.scene._matrix.manual = true;
    // this.two.scene._matrix.translate(0, this.two.height).scale(1, 1);
    // this.two.scene._matrix.translate(0, 0).scale(1, 1);

    if ( window ) {
      window.two = this.two;
      window.Two = Two;
      window.math = math;
    }

    this.reset();
    this.run();
  },
  beforeDestroy() {
    this.stop();
    this.reset();
  },
  destroyed() {
    window.cancelAnimationFrame(this.animationId);
    this.animationId = undefined;
  },
  methods: {
    reset() {
      // this.stop();
      // Object.assign( this, initState() );
      this.agents = [];
      this.environment = {};
      this.snapshots = [];
      this.two.clear();
      this.time = 0;
      this.resetStats()
      this.init();
    },
    init() {
      for (let i = 0; i < 150; i++ ) {
        let circle = this.two.makeCircle(20, 50, 5);
        circle.fill = 'orange';
        this.agents[i] = new Agent(i, circle, this.two);
      }

      this.environment = new Environment(this.agents, this.params);
      this.agents.forEach(agent => agent.environment = this.environment)
      this.step(0)
      this.draw()
    },
    step() {
      this.time += 1;
      this.environment.updateDistanceMatrix();
      this.agents.forEach(agent => agent.step(this.time))
      let summary = this.environment.getSummary();
      this.numTotal    = summary.total;
      this.numInfected = summary.infected;
      this.numHealed   = summary.healed;
      // this.snapshots.push({...summary});
      this.updateStats(summary)
    },
    draw() {
      this.agents.forEach(agent => agent.draw())
      this.two.update();
    },
    stop() {
      clearInterval( this.interval );
      this.status = false;
      // this.reset();
      // this.draw();
    },
    run(steps = Infinity) {
      this.stop();
      this.status = true;
      let n = 1;
      this.interval = setInterval(() => {
        this.step(); // HACK
        this.animationId = window.requestAnimationFrame(this.draw);
        n += 1;
        if ( steps && n > steps ) {
          this.stop();
        }
      }, 25);
    },
    resetStats() {
      const canvas = this.progressCanvas.renderer.ctx.canvas;
      if (canvas.getContext) {
        let ctx = canvas.getContext('2d');
        ctx.fillStyle = 'white';
        ctx.fillRect(0, 0, 8000, 8000)
      }
    },
    updateStats(summary) {
      const canvas = this.progressCanvas.renderer.ctx.canvas;
      if (canvas.getContext) {
        let ctx = canvas.getContext('2d');
        const diff = summary.total - summary.infected - summary.healed;
        const s = 160 / this.agents.length;
        ctx.fillStyle = 'red';
        ctx.fillRect(this.time, 0, 1, s * summary.infected);
        ctx.fillStyle = 'blue';
        ctx.fillRect(this.time, s * summary.infected, 1, s * summary.healed);
        ctx.fillStyle = 'lightgreen';
        ctx.fillRect(this.time, s * (summary.infected + summary.healed), 1, s * diff);
      }
    }
  },
}
</script>

